@use "./variables" as v;

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: v.$violet;
  background-image: url("/images/bg-desktop.svg");
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: v.$body_font_family;
  font-weight: v.$body_font_weight;
  font-size: 16px;
  line-height: 1.5;
  @include v.mobile {
    background-image: url("/images/bg-mobile.svg");
  }
}

h1 {
  font-family: v.$heading_font_family;
  font-weight: v.$heading_font_weight;
  @include v.mobile {
    text-align: center;
  }
  @include v.laptop {
    padding-left: 90px;
    padding-right: 90px;
  }
}

h1,
p {
  color: v.$white;
}

p {
  @include v.mobile {
    text-align: center;
    margin: 0px 30px 30px 30px;
    word-spacing: 6.3px;
  }
  @include v.laptop {
    padding-left: 90px;
    padding-right: 20px;
  }
}

button {
  border-radius: 50px;
  border: none;
  color: v.$violet;
  @include v.mobile {
    display: block;
    height: 45px;
    width: 206px;
    margin: auto;
    margin-bottom: 30px;
  }
  @include v.laptop {
    height: 34px;
    width: 102px;
    margin-left: 90px;
  }
}

button:hover {
  background-color: v.$soft_magneta;
  color: v.$white;
}

.logo-image {
  height: 32px;
  display: block;
  margin-top: 31px;
  @include v.laptop {
    margin-bottom: 51px;
  }
}

.illustration-image {
  max-width: 100%;
  @include v.mobile {
    margin-top: 68px;
    margin-bottom: 74px;
    margin-right: auto;
    margin-left: auto;
    display: block;
  }
  @include v.laptop {
    margin-top: 65px;
  }
}
.icons {
  @include v.mobile {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  @include v.laptop {
    position: relative;
    left: 340px;
    top: 240px;
  }
}

i.fa {
  color: v.$white;
  display: inline-block;
  border-radius: 50px;
  box-shadow: 0 0 2px v.$white;
  padding: 0.5rem 0.6rem;
  margin-right: 10px;
}

i.fa:hover {
  color: v.$soft_magneta;
  display: inline-block;
  border-radius: 50px;
  box-shadow: 0 0 2px v.$soft_magneta;
  padding: 0.5em 0.6em;
  margin-right: 10px;
}

.grid-container {
  @include v.laptop {
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
    align-items: center;
  }
}

.logo-image,
.illustration-image {
  @include v.laptop {
    margin-left: 65px;
  }
}

.right-item {
  @include v.laptop {
    margin-left: 80px;
    margin-right: 50px;
  }
}
